<template>
    <div class="container">
        <h2>inputNumber 数字输入框组件</h2>
        <h3>数字输入框组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'button',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'value',
                    desc: '输入框当前值，开发者需要通过 onChange 事件来更新 value 值，必填',
                    type: 'Number,String',
                    optional: '-',
                    default: '1'
                },
                {
                    param: 'min',
                    desc: '最小值',
                    type: 'Number',
                    optional: '-',
                    default: '1'
                },
                {
                    param: 'max',
                    desc: '最大值',
                    type: 'Number',
                    optional: '-',
                    default: '100'
                },
                {
                    param: 'step',
                    desc: '每次点击改变的间隔大小',
                    type: 'Number',
                    optional: '-',
                    default: '1'
                },
                {
                    param: 'disabledInput',
                    desc: `禁止输入，但不禁止点击按钮`,
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                },
                {
                    param: 'disabled',
                    desc: `是否禁止输入，禁止点击按钮`,
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                }
            ],
            events: [
                {
                    name: 'onChange',
                    desc: `输入框值改变时触发的事件，开发者需要通过 onChange 事件来更新 value 值变化，onChange 函数必填`,
                    param: `输入框当前值 value`
                },
                {
                    name: 'onBlur',
                    desc: `输入框值失去焦点时触发的事件`,
                    param: `event`
                },
                {
                    name: 'onErrorInput',
                    desc: `输入框尝试输入错误数组触发的事件`,
                    param: `({ type: OVER | LOW | DISABLED, errorValue: number})`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiInputNumber,
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {
        constructor(props){
            super(props)
            this.state = {
                val:10
            }
        }

        testOnErr(err){
            console.log(err)
        }

        testOnBlur(value){
            this.setState({
                val:value
            })
            console.log('当前值为',value)
        }

        render () {
          return (
              <View className='index'>
                  <WiInputNumber
                      min={1}
                      step={1}
                      value={this.state.val}
                      onErrorInput={this.testOnErr.bind(this)}
                      onBlur={this.testOnBlur.bind(this)}
                      onChange={this.testOnBlur.bind(this)}
                  />
              </View>
          )
        }
    }
<\/script>`
        }
    },
}
</script>

<style lang="scss" scoped>
.container {
    padding-bottom: 50px;
}
</style>
